<template>
   <group class="jq-mt">
     <flexbox class="jian-bg">
       <flexbox-item class='jia-bg'>
         <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
       </flexbox-item>
       <flexbox-item class='jia-bg'>
         <popup-picker :title="busTitle"  :data="costList" :value.sync="cost"></popup-picker>
       </flexbox-item>
     </flexbox>
  </group>
    <group class="jq-mt">
     <div class="chart" v-ebar="barData"></div>
    </group>

</template>
  <script>
    import {Flexbox, FlexboxItem,Group,cell,PopupPicker} from 'vux/src/components'
    import { getList } from '../../../untils/api'
    export default {
    components: {
    Flexbox,
    FlexboxItem,
    cell,
    PopupPicker,
    Group
    },
    data () {
    return {
    //busTitle:'事业部',
    cost:['物料'],
    costList:[formatData.costList],
    dateTile:'日期',
    time: formatData.defaultTime,
    timeList: formatData.dateList,
    barData:{}
    }
    },
    ready(){
      document.title='成本构成';
    },
    methods: {
    async getData() {
        commit('UPDATE_LOADING', true)
    let id ='APP10205';
    let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
    
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barData = {};
    this.barData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
       watch: {
     time (newtime, oldtime) {
      this.getData();
       formatData.setDateTime(newtime);
    }
  },
    created: function () {
    this.barData = {};
    this.barData.dataset = [];
    this.getData();
    }
    }
  </script>






